<template>
    <div>
        <input @input="updateValue" :value="value" v-bind="$attrs" v-on="listeners" />
        <!-- <p style="white-space: pre-line;">{{ message }}</p> -->
    </div>
</template>

<script>
    export default {
        name: "BaseInput.vue",
        inheritAttrs: false, // values the root of the div inheriting the attributes
        props: {
            value: [String, Number]
        },
        methods: {
            updateValue(event){
                // listening for events(i.e the input field) to be typed in the child component and then
                // we pass in the input typed value and send it to the parent component
                this.$emit('input', event.target.value);
            }
        },
        computed: {
            listeners(){
                return {
                    ...this.$listeners,
                    input: this.updateValue
                }
            }
        }
    }
</script>

