<template>
    <div class="about">
        <h1>This is an Contact page</h1>
        <form @submit.prevent="submitForm">
            <span>Phone Number: <a :href="`tel:${contactFormDetails[0].telephone}`">{{contactFormDetails[0].telephone}}</a></span>
            <span>Email: <a :href="`mailto:${contactFormDetails[0].emailadrress}`">{{contactFormDetails[0].emailadrress}}</a></span>

            <BaseInput v-model.trim="contactFormDetails.subject"
                       type="text"
                       placeholder="Subject"
                       :class="{error: $v.contactFormDetails.subject.$error}"
                       @blur="$v.contactFormDetails.subject.$touch()" lazy>
            </BaseInput>
            <template v-if="$v.contactFormDetails.subject.$error">
                <p v-if="!$v.contactFormDetails.subject.required" class="errorMessage">Subject is required</p>
            </template>
            <BaseInput v-model.trim="contactFormDetails.fullname"
                       type="text"
                       placeholder="Fullname"
                       :class="{error: $v.contactFormDetails.fullname.$error}"
                       @blur="$v.contactFormDetails.fullname.$touch()" lazy>
            </BaseInput>
            <template v-if="$v.contactFormDetails.fullname.$error">
                <p v-if="!$v.contactFormDetails.fullname.required" class="errorMessage">Name is required</p>
            </template>
            <BaseInput v-model.trim="contactFormDetails.email"
                       type="email"
                       placeholder="Email"
                       :class="{error: $v.contactFormDetails.email.$error}"
                       @blur="$v.contactFormDetails.email.$touch()" lazy>
            </BaseInput>
            <template v-if="$v.contactFormDetails.email.$error">
                <p v-if="!$v.contactFormDetails.email.required" class="errorMessage">Email is required</p>
                <p v-else-if="!$v.contactFormDetails.email.email" class="errorMessage">Please enter a valid email address</p>
            </template>
            <BaseInput v-model.trim="contactFormDetails.phonenumber"
                       type="tel"
                       placeholder="Phonenumber"
                       :class="{error: $v.contactFormDetails.phonenumber.$error}"
                       @blur="$v.contactFormDetails.phonenumber.$touch()" lazy>
            </BaseInput>
            <template v-if="$v.contactFormDetails.phonenumber.$error">
                <p v-if="!$v.contactFormDetails.phonenumber.isPhone" class="errorMessage">Enter a valid phone number</p>
            </template>
            <textarea v-model="contactFormDetails.message"
                      :style="{'white-space':textareaLine}"
                      @blur="$v.contactFormDetails.message.$touch()"
                      placeholder="Please type your message">
            </textarea>
            <template v-if="$v.contactFormDetails.message.$error">
                <p v-if="!$v.contactFormDetails.message.required" class="errorMessage">Please write a message</p>
            </template>
            <BaseButton @click="validate"  type="submit">Submit</BaseButton>
            <template v-if="success">
                <p class="errorMessage">{{errorMsg}}</p>
            </template>
        </form>
    </div>
</template>

<script>
    import { required, email, helpers} from 'vuelidate/lib/validators'
    //const isPhone = (value) => !helpers.req(value) || /^1([345678])\d{9}$/.test(value);
    const isPhone = (value) => !helpers.req(value) || /^[+]?[(]?[0-9]{3}[)]?[-\s]?[0-9]{3}[-\s]?[0-9]{4,6}$/im.test(value);
    export default {


        data(){
            return {
                contactFormDetails: [
                    {
                        telephone: 744389999,
                        emailadrress: 'xxxx@gmail.com'
                    },
                    {
                        subject: '',
                        fullname: '',
                        email: '',
                        phonenumber: '',
                        message: ''
                    }
                ],
                textareaLine: 'pre-line',
                success: false,
                errorMsg: 'Please fill out the required field(s).'

            }
        },

        validations: {
            contactFormDetails:
                {
                    subject: {required},
                    fullname: {required},
                    email: {required, email},
                    phonenumber: {isPhone},
                    message: {required}
                }
        },

        methods: {
            submitForm(){
                console.log('About to Submit');
                this.$v.$touch();
                if (!this.$v.$invalid) {
                    this.success = false;
                    console.log('Send to the server to for server validation');
                }
                else{
                    this.success = true;
                    console.log('There is an error in the submission');
                }
            },

            // look into this code again.
            // https://markus.oberlehner.net/blog/vue-form-validation-with-vuelidate/
            focusComponentError(component = this){
                if (component.error){
                    component.$el.focus();
                    return true;
                }

                let focused = false;
                component.$children.some((childComponent) => {
                    focused = this.focusComponentError(childComponent);
                    return focused;
                });

                return focused;
            },

            validate(){
                this.$v.$touch();
                this.$nextTick(() => this.focusComponentError());
            }
        },

    }
</script>

<style scoped>
    /* error class to be added here.. It was dynamic binded above :error */
    .error {
        border: 1px solid red;
    }

    .errorMessage {
        color: red;
    }

</style>